.modal {
  position: fixed;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  z-index: 1000;

  display: flex;
  flex-direction: column;
  overflow-y: auto;
  -webkit-overflow-scrolling: touch;

  background-color: rgba($color-default-black, 0.8);
  transform: scale(1.2);
  visibility: hidden;
  opacity: 0;

  transition: opacity $trans-modal,
    transform $trans-modal,
    visibility $trans-modal;

  pointer-events: none;

  -ms-overflow-style: none;
  scrollbar-width: none;

  &::-webkit-scrollbar {
    display: none;
  }

  &__wrapper {
    position: relative;

    display: flex;
    flex-shrink: 0;
    justify-content: center;
    align-items: center;
    width: 100%;
    min-height: 100%;
    padding: 60px 40px;

    @include vp-767 {
      padding: 60px 16px;
    }
  }

  &__overlay {
    position: absolute;
    top: 0;
    left: 0;

    width: 100%;
    height: 100%;

    cursor: pointer;
  }

  &__content {
    position: relative;

    width: 600px;
    padding: 40px;

    background-color: $color-default-white;

    @include vp-767 {
      width: 100%;
      padding: 20px;
    }
  }

  &__close-btn {
    position: absolute;
    top: 22px;
    right: 22px;
    z-index: 1;

    width: 40px;
    height: 40px;
    padding: 0;

    background: $color-transparent;
    border: none;
    cursor: pointer;
  }

  &--responsive {
    .modal__content {
      width: 100%;
    }
  }

  &--fit-content {
    .modal__content {
      width: auto;

      @include vp-767 {
        width: 100%;
      }
    }
  }

  &--no-scale {
    transform: none;
  }

  &--preload {
    transition: none;
  }

  &.is-active {
    transform: scale(1);
    visibility: visible;
    opacity: 1;

    pointer-events: auto;
  }
}

.mymodal-wrapper {

}
.mymodal__headline {
  margin-bottom: 20px;
  margin-top: 0;
  width: 100%;
}
.mymodal__header {
  color: var(--dark, #001017);
  /* 44 Med */
  font-family: Muller;
  font-size: 44px;
  font-style: normal;
  font-weight: 500;
  line-height: 140%; /* 61.6px */
  letter-spacing: 2.2px;
  text-transform: uppercase; 
  padding-bottom: 20px;
  border-bottom: 1px solid #001017;
  display: block;
}
.mymodal__text {

}
.mymodal__form {
 
}
.mymodal__from-wrap {
  margin-bottom: 20px;
  &.--grid {
    display: grid;
    grid-template-columns: repeat(2,1fr);
    gap: 30px 40px;

    input {
      width: 100%;
    }
  }
  &.--grid-shitty {
    display: grid;
    grid-template-columns: repeat(2,1fr);
    gap: 30px 40px;

    .mymodal__from-label:nth-child(1),
    .mymodal__from-label:nth-child(4) {
      grid-column-start: 1;
      grid-column-end: 3;
    }

    input {width: 100%;}
  }
}
.mymodal__field {
  border-radius: 4px;
  border: 2px solid var(--dark, #001017);
  background: var(--white, #FFF); 
  color: var(--dark, #001017);
  font-size: 24px;
  font-style: normal;
  font-weight: 300;
  line-height: normal;  
  padding: 10px;
  box-sizing: border-box;
  width: 100%;
}
.mymodal__form-bottom {
  display: flex;
  justify-content: start;
  align-items: center;
}
.mymodal__submit {
  margin-right: 20px;
  padding: 15px 20px !important;
}
.mymodal__terms {
  line-height: 100% !important;
  font-size: 14px !important;
  font-weight: 400 !important;
}

[data-modal="good"],
[data-modal="vacancy"] {
  .modal__content {
    max-width: 990px;
    width: 100%;
  }
}
[data-modal="success"] {
  .modal__content {
    max-width: 600px;
    width: 100%;
  }
}
.mymodal__from-label {
  position: relative;
}
.mymodal__file {
  position: absolute;
  visibility: hidden;
}
.mymodal-wrapper .mymodal__file-custom-file {
  padding: 15px;
  display: flex;
  justify-content: center;
  align-items: center;
  font-size: 18px;

  img {
    margin-right: 20px;
  }
}
.mymodal__file-info {
  display: none;
  justify-content: space-between;
  align-items: center;
  margin-top: 20px;

  &.is-active {
    display: flex;
  }
  span {
    &:nth-child(2) {
      opacity: 0.5; 
    }
  }
}
.mymodal__file-clear {

}

@media screen and (max-width: 900px) {
  .mymodal__from-wrap.--grid,
  .mymodal__from-wrap.--grid-shitty {
    grid-template-columns: 1fr;
    gap: 15px;
  }
  .mymodal__header {
    font-size: 20px; 
    font-weight: 500;
    margin-top: 10px;
  }
}