.header {
  padding: 26px 0 27px;
  background: #fff;
  .header__wrapper {
    display: flex;
    justify-content: space-between;
    align-items: center;
  }
  .header__logo {
  
  }
  .header__logo img {
  
  }
  
  .header__wrap {
    display: flex;
    justify-content: start;
    align-items: center;
  }
  .header__nav {
    .header__btn {
      display: none;
    }
  }
  .header__nav-list {
    display: flex;
    justify-content: start;
    align-items: center;
    list-style-type: none;
  }
  .header__nav-item {
    padding: 0;

    &.--dropdown {
      position: relative;

      &:hover {
        .header__nav-item__sub {
          display: block;

        }
      }
      & > span {
        display: flex;
        justify-content: start;
        align-items: center;
        &::after{
          content: '';
          width: 12px;
          height: 12px;
          background-image: url('../img/svg/dropdown.svg');
          display: block;
          margin-left: 5px;
        }
      }
    }
    .header__nav-item__sub {
      display: none;
      position: absolute;
      top: 100%;
      width: max-content;
      background: #fff;
      border: 1px solid #000;
      padding: 20px 0;

      a {
        display: block;
        &:not(:last-child) {
          margin-bottom: 20px;
        }
        padding-left: 21px;
        padding-right: 21px;
      }
    }

    a, span {
      color: #001017;
      font-size: 18px;
      font-family: Muller;
      font-style: normal;
      font-weight: 500;
      line-height: normal;       
    }
    &:not(:last-child) {
      margin-right: 40px;
    }    
  }
  .header__link {
    display: flex;
    justify-content: start;
    align-items: center;

    &:not(:first-child) {
      margin-left: 54px;
    }

    img {
      margin-right: 10px;
    }
  }
  .header__btn {
    margin-left: 30px;
    font-size: 18px;
    padding: 15px 30px;
  }
}

.header__burger {
  margin-left: 25px;
  display: none;

  &.is-active {
    .header__burger-wrap {
      &::before,
      &::after {
        top: 50%;
        transform: translateY(-50%) rotate(45deg);
        width: 100%;
      }
      &::after {
        transform: translateY(-50%) rotate(-45deg);
      }

      .header__burger-item {
        opacity: 0;
      }
    }
  }
}
.header__burger-wrap {
  width: 15px;
  height: 15px;
  display: flex;
  justify-content: center;
  align-items: center;
  position: relative;

  &::before,
  &::after {
    content: '';
    width: 100%;
    height: 2px;
    border-radius: 1px;
    background: #000;
    display: block;

    position: absolute;
    top: 0;
    right: 0;
  }
  &::after {
    top: unset;
    bottom: 1px;
    width: 70%;
  }
}
.header__burger-item {
  width: 100%;
  height: 2px;
  background: #000;
  border-radius: 1px;
}

@media screen and (max-width: 600px) {
  .header__burger {
    display: block;
  }
}